<template>
  <div class="top-menu">
    <i @click="drawer = true" class="el-icon-s-tools"></i>
    <el-drawer
      size="60%"
      title="我是标题"
      :visible.sync="drawer"
      :with-header="false"
    >
      <div class="setBox" v-if="!isView" @click="goView">
        <img
          class="icon"
          src="https://vkceyugu.cdn.bspapp.com/VKCEYUGU-ae070c7f-068d-4e5c-adf5-baf6760c9a2b/f12d7833-6d9f-4328-b1a1-f3c1dbcf3d2d.png"
          alt=""
        />
        进入全屏
      </div>
      <div class="setBox" v-else @click="goView">
        <img
          class="icon"
          src="https://vkceyugu.cdn.bspapp.com/VKCEYUGU-ae070c7f-068d-4e5c-adf5-baf6760c9a2b/d90cfcc7-4ce1-4db5-9125-fbe8fe08a135.png"
          alt=""
        />
        退出全屏
      </div>
      <div class="setBox">
        <img
          class="icon"
          src="https://vkceyugu.cdn.bspapp.com/VKCEYUGU-ae070c7f-068d-4e5c-adf5-baf6760c9a2b/02a78e40-de3c-41ae-a770-0416536885c8.png"
          alt=""
        />
        个人主页
      </div>
      <div class="setBox">
        <img
          class="icon"
          src="https://vkceyugu.cdn.bspapp.com/VKCEYUGU-ae070c7f-068d-4e5c-adf5-baf6760c9a2b/a51591d3-f223-4d63-833e-9b2f520c4033.png"
          alt=""
        />
        登陆
      </div>
      <div class="setBox">
        <img
          class="icon"
          src="https://vkceyugu.cdn.bspapp.com/VKCEYUGU-ae070c7f-068d-4e5c-adf5-baf6760c9a2b/a51591d3-f223-4d63-833e-9b2f520c4033.png"
          alt=""
        />
        退出登陆
      </div>
    </el-drawer>
  </div>
</template>

<script>
export default {
  name: "TopMenu",
  data() {
    return {
      drawer: false,
      isView: false,
    };
  },

  methods: {
    goView() {
      console.log(1);
      if (this.isView) {
        if (document.exitFullscreen) {
          document.exitFullscreen();
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        } else if (document.webkitExitFullscreen) {
          document.webkitExitFullscreen();
        }
      } else {
        var element = document.documentElement;
        if (element.requestFullscreencreen) {
          element.requestFullScreen();
        } else if (element.mozRequestFullScreen) {
          element.mozRequestFullScreen();
        } else if (element.webkitRequestFullScreen) {
          element.webkitRequestFullScreen();
        } else if (element.msRequestFullScreen) {
          element.msRequestFullScreen();
        }
      }
      this.isView = !this.isView;
    },
  },
};
</script>

<style scoped>
/* 导航栏start */
.top-menu {
  padding: 0 15px;
  box-sizing: border-box;
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: right;
  background-color: black;
  overflow: auto;
  border-bottom: 1px #262626 solid;
}
</style>